import type { FieldErrorState, FieldValue, FormsState } from "@/types/forms";
import { defineStore } from "pinia";

const useFormsStore = defineStore("forms", {
  state: () =>
    ({
      forms: {},
    } as FormsState),
  getters: {
    getFormFields:
      (state: FormsState) =>
      (formId: string): Record<string, FieldValue> | undefined =>
        state.forms[formId]?.fields,
    getFormRawValues:
      (state: FormsState) =>
      (formId: string): Record<string, unknown> | undefined => {
        if (!state.forms[formId]) {
          return undefined;
        }
        const result: Record<string, unknown> = {};
        for (const fieldName in state.forms[formId].fields) {
          result[fieldName] = state.forms[formId].fields[fieldName].value;
        }
        return result;
      },
    getErrorsStatus:
      (state: FormsState) =>
      (formId: string): FieldErrorState[] | undefined => {
        const errorState: FieldErrorState[] = [];
        if (!state.forms[formId]) {
          return undefined;
        }
        for (const fieldName in state.forms[formId].fields) {
          const currentField = state.forms[formId].fields[fieldName];
          if (currentField.error == true) {
            errorState.push({
              fieldName,
              fieldCurrentValue: currentField.value,
              fieldOriginalValue: currentField.originalValue,
              errorDescription: currentField.errorDescription ?? "UNKNOWN",
            });
          }
        }
        return errorState;
      },
    getField:
      (state: FormsState) =>
      (formId: string, fieldName: string): FieldValue | undefined =>
        state.forms[formId]?.fields[fieldName],
    getFieldValue:
      (state: FormsState) =>
      (formId: string, fieldName: string): unknown | undefined =>
        state.forms[formId]?.fields[fieldName]?.value,
    getFieldErrorStatus:
      (state: FormsState) =>
      (formId: string, fieldName: string): boolean | undefined =>
        state.forms[formId]?.fields[fieldName]?.error,
    getFieldErrorMessage:
      (state: FormsState) =>
      (formId: string, fieldName: string): string | undefined =>
        state.forms[formId]?.fields[fieldName]?.errorDescription,
  },
  actions: {
    setFieldValue(
      formId: string,
      fieldName: string,
      value?: unknown,
      required?: boolean,
      error?: boolean,
      errorMessage?: string
    ) {
      if (!this.forms[formId]) {
        this.forms[formId] = { fields: {} };
      }
      if (!this.forms[formId].fields[fieldName]) {
        this.forms[formId].fields[fieldName] = {
          originalValue: value,
        };
      }
      const field = this.forms[formId].fields[fieldName];
      if (typeof error != "undefined") {
        field.error = error;
        field.errorDescription = errorMessage;
      }
      field.value = value;
      field.required = required;
    },
    setFieldError(
      formId: string,
      fieldName: string,
      error: boolean,
      errorMsg?: string
    ) {
      const field = this.forms[formId].fields[fieldName];
      if (!field) {
        return;
      }
      field.error = error;
      field.errorDescription = errorMsg;
    },
    validateForm(formId: string) {
      const form = this.forms[formId].fields ?? {};
      for (const fieldName in form) {
        const field = form[fieldName];
        if (field.required && !field.value) {
          field.error = true;
          field.errorDescription = "fieldRequired";
        }
      }
    },
    resetFormErrors(formId: string) {
      const form = this.forms[formId].fields ?? {};
      for (const fieldName in form) {
        form[fieldName].error = undefined;
        form[fieldName].errorDescription = undefined;
      }
    },
    resetFormValues(formId: string) {
      const form = this.forms[formId].fields ?? {};
      for (const fieldName in form) {
        form[fieldName].value = form[fieldName].originalValue;
      }
    },
  },
});

export default useFormsStore;
