import cache from "@/cache/cache";
import type { Reference, ReferencesState } from "@/types/references";
import { defineStore } from "pinia";

const useReferencesStore = defineStore("references", {
  state: () =>
    ({
      references: {},
    } as ReferencesState),
  getters: {},
  actions: {
    async fetchReference(tableName: string): Promise<Reference | undefined> {
      if (this.references[tableName]) {
        return this.references[tableName];
      }
      const reference = await cache.references.getReference(tableName);
      if (reference) {
        this.references[tableName] = reference;
      }
      return reference;
    },
  },
});

export default useReferencesStore;
